
<template>
    <div class="row">
        <div class="col-75">
            <div class="container">

        <form @submit.prevent="submitHandler()">
            <div class="form">
            <label>Фамилия<i class="required">*</i></label>
            <input type="text" v-model.trim="$v.lastname.$model"
                   :class="{invalid:($v.lastname.$error)}"><br/>

            <label>Имя<i class="required">*</i></label>
            <input type="text" v-model.trim="$v.firstname.$model"
                   :class="{'invalid':($v.firstname.$error)}"> <br/>

            <label>Отчество</label>
            <input type="text" v-model.trim="$v.middlename.$model"><br/>
                <label style="float: left"> Пол </label>
                <select required style="float: right">
                    <option v-for="n in ['','Мужской','Женский']"  :value="n"  v-bind:key="n" >{{ n }}</option>
                </select>
                <br/>
                <label>Группа клиентов<i class="required">* </i></label>
                <select  v-model="group" required v-model.trim="$v.group.$model" :class="{'invalid':($v.group.$error)}">
                    <option v-for="n in ['','VIP', 'Проблемные', 'ОМС']"  :value="n"  v-bind:key="n" >{{ n }}</option>
                </select><br/>
                <label>Лечащий врач:</label>
                <select style="float:right;">
                    <option v-for="n in ['Иванов', 'Чернышева', 'Захарова']"  :value="n"  v-bind:key="n" >{{ n }}</option>
                </select>
            </div>

            <div class="form">
            <label>Дата рождения<i class="required">*</i></label>
                <input type="date" v-model="selectBDate" :class="{invalid:($v.selectBDate.$error)}" v-validate="'required|date_format:DD-MM-YYYY|earlier'"><br/>
                <label>Телефон: &nbsp; &nbsp;&nbsp; +7</label>
                <input type="text" style="float: left;" v-model.trim="$v.phone.$model"
                       :class="{invalid:($v.phone.$error)}"><br/>
                <label> Не отправлять SMS: <input type="checkbox"></label>
            </div>

            <div class="form">
                <label>Индекс</label>
                <input type="text"><br>
                <label>Страна</label>
                <input type="text"><br>
                <label>Область</label>
                <input type="text"><br>
                <label>Город<i class="required">*</i></label>
                <input type="text" v-model.trim="city" :class="{invalid:($v.city.$error)}" @blur="$v.city.$touch()"><br>
                <label>Улица</label>
                <input type="text"><br>
                <label>Дом</label>
                <input type="text">
            </div>

            <div class="form">
                <label> Тип документа<i class="required">* </i></label>
                <select style="margin-left: 1rem;" :class="{invalid:($v.selectPassport.$error)}">
                    <option v-for="n in ['','Паспорт', 'Свидетельство о рождении', 'Вод. удостоверение']"  :value="n"  v-bind:key="n" >{{ n }}</option>
                </select><br/>
                <span>Серия</span>
                <input type="text"><br>
                <label>Номер</label>
                <input type="text"><br>

                <label>Кем выдан</label>
                <input type="text"><br>

                <label>Дата выдачи<i class="required">*</i></label>
                <input type="date" required :class="{invalid:($v.passportDate.$error)}">
            </div>

            <hr width="100%"/>
            <button type="submit">Создать аккаунт</button>
        </form>

    <div class="invalid-feedback" v-if="$v.firstname.$error || $v.lastname.$error || $v.middlename.$error">
        <template v-if="!$v.firstname.alpha || !$v.lastname.alpha || !$v.middlename.alpha">
            <p> Имя, фамилия и отчество должны содержать только кирилицу!</p>
        </template>
        <template v-else>
            <p>Имя и фимилия обязательны для заполнения!</p>
        </template>
    </div>

    <div class="invalid-feedback" v-if="$v.group.$error">
        <template>
            <p> Выберите группу клиентов!</p>
        </template>
    </div>
    <div class="invalid-feedback" v-if="$v.selectBDate.$error">
        <template>
            <p> Заполните дату рождения!</p>
        </template>
    </div>
    <div class="invalid-feedback" v-if="$v.phone.$error">
        <template v-if="!$v.phone.alpha || !$v.phone.maxLength || !$v.phone.minxLength">
            <p> Это поле должно содержать номер телефона в формате 10 цифр</p>
        </template>
    </div>
    <div class="invalid-feedback" v-if="$v.city.$error">
        <template>
            <p> Заполните пожалуйста поле "Город"</p>
        </template>
    </div>
    <div class="invalid-feedback" v-if="$v.passportDate.$error">
        <template>
            <p> Заполните пожалуйста поле "Дата выдачи"</p>
        </template>
    </div>

            </div>
        </div>
    </div>


</template>

<script>
import {required, minLength, maxLength} from 'vuelidate/lib/validators'

    export default {
        name: 'FormValidation',
        data: () => ({
            firstname:'',
            lastname:'',
            middlename: '',
            selectBDate:'',
            selectGetDate:'',
            selectPassport:'',
            passportData: '',
            city:'',
            phone: '',
            group: '',
            passportDate:''
        }),
        validations: {
            selectPassport: {
                required
            },
            passportData: {
                required,
                validFormat: val => /^\d{4} \d{6}$/.test(val),
            },
            firstname: {
                required,
                alpha: val => /^[а-яё]*$/i.test(val),
            },
            lastname: {
                required,
                alpha: val => /^[а-яё]*$/i.test(val),
            },
            middlename: {
                alpha: val => /^[а-яё]*$/i.test(val),
            },
            selectBDate: {
                    required
            },
            selectGetDate:
                {
                    required
                },
            phone: {
                    validFormat: val => /^\d{10}$/.test(val),
                    minLength: minLength(10),
                    maxLength: maxLength(10)
            },
            group:{
                    required,
            },
            city: {
                required
            },
            passportDate: {
                required
            }
        },
        methods: {
            submitHandler() {
                if(this.$v.$invalid)
                {
                    this.$v.$touch()
                    return
                }
                this.$router.push('/')
            },
            getNumbers:function(start,stop){
                return new Array(stop-start).fill(start).map((n,i)=>n+i);
            }
        }
    }
</script>
